export const ONLINEVIDEO_INSERT = Symbol('ONLINEVIDEO_INSERT');

export const ONLINEVIDEO_REMOVE = Symbol('ONLINEVIDEO_REMOVE');

export const ONLINEVIDEO_UPDATE = Symbol('ONLINEVIDEO_UPDATE');

export const ONLINEVIDEO_FOCUS_EDITOR = Symbol('ONLINEVIDEO_FOCUS_EDITOR');

export const ONLINEVIDEO_SELECT = Symbol('ONLINEVIDEO_SELECT');

export function insertOnlineVideo() {
  return {
    type: ONLINEVIDEO_INSERT,
  };
}

export function removeOnlineVideo(node) {
  return {
    type: ONLINEVIDEO_REMOVE,
    payload: {
      node,
    },
  };
}

export function updateOnlineVideo(onlineVideo, data) {
  return {
    type: ONLINEVIDEO_UPDATE,
    payload: {
      node: onlineVideo,
      data,
    },
  };
}

export function focusOnlineVideoEditor(node) {
  return {
    type: ONLINEVIDEO_FOCUS_EDITOR,
    payload: {
      node,
    },
  };
}

export function selectOnlineVideo(node) {
  return {
    type: ONLINEVIDEO_SELECT,
    payload: {
      node,
    },
  };
}
